:root{
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  }
html, body{
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 14px;
  padding: 0;
  margin: 0;
  height: 100vh;
  }
body{
  background: var(--color-basic);
  color: #427dad;
  }
.page{
  overflow: hidden;
  width: 100%;

  }
ul,
li{
  margin: 0;
  padding: 0;
  }
a{
  text-decoration: none;
  color: inherit;
  }
section{

  margin-bottom: 10px;
  padding: 2px;
  }
.mobile-container{
  width: 320px;
  padding: 0 12px;
  background: #000;
  margin: 0 auto;
  }
.pc-container{
  width: 1200px;
  padding: 20px;
  margin: 0 auto;
  }
.mobile-container{

  margin-bottom: 10px;
  padding: 8px;
  .skills{
    &-img{
      width: 100%;
      }
    &-heading{
      text-transform: uppercase;
      text-align: center;
      }
    }
  .logo{
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1.2;
    padding: 10px;
    font-size: 36px;
    letter-spacing: 4px;
    font-weight: 900;
    text-transform: uppercase;
    flex-direction: column;
    span{
      display: inline-block;
      font-size: 14px;
      font-weight: 400;
      line-height: 1.2;
      }
    }
  .menu{
    margin-bottom: 24px;
    ul{
      padding: 6px 0;
      li{
        text-align: left;
        list-style: none;
        margin-bottom: 2px;
        border-left: 1px solid #1a3246;
        &:last-child{
          margin-bottom: 0;
          }
        img{
          //width: 100%;
          height: 70%;
          object-fit: contain;
          margin-right: 20px;
          }
        a{
          display: flex;
          align-items: center;
          justify-content: center;
          height: 40px;
          width: 100%;
          background: #122331;
          text-transform: uppercase;
          font-weight: 600;
          &:hover{
            background: #363636;
            }
          &:active{
            background: #555;
            }
          }
        }

      }
    }
  }
.wrapper{
  height: 100vh;
  }