:root {
  --color-basic: #09131b;
}

:root {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 14px;
  padding: 0;
  margin: 0;
  height: 100vh;
}

body {
  background: var(--color-basic);
  color: #427dad;
}

.page {
  overflow: hidden;
  width: 100%;
}

ul,
li {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

section {
  margin-bottom: 10px;
  padding: 2px;
}

.mobile-container {
  width: 320px;
  padding: 0 12px;
  background: #000;
  margin: 0 auto;
}

.pc-container {
  width: 1200px;
  padding: 20px;
  margin: 0 auto;
}

.mobile-container {
  margin-bottom: 10px;
  padding: 8px;
}

.mobile-container .skills-img {
  width: 100%;
}

.mobile-container .skills-heading {
  text-transform: uppercase;
  text-align: center;
}

.mobile-container .logo {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1.2;
  padding: 10px;
  font-size: 36px;
  letter-spacing: 4px;
  font-weight: 900;
  text-transform: uppercase;
  flex-direction: column;
}

.mobile-container .logo span {
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
}

.mobile-container .menu {
  margin-bottom: 24px;
}

.mobile-container .menu ul {
  padding: 6px 0;
}

.mobile-container .menu ul li {
  text-align: left;
  list-style: none;
  margin-bottom: 2px;
  border-left: 1px solid #1a3246;
}

.mobile-container .menu ul li:last-child {
  margin-bottom: 0;
}

.mobile-container .menu ul li img {
  height: 70%;
  object-fit: contain;
  margin-right: 20px;
}

.mobile-container .menu ul li a {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  width: 100%;
  background: #122331;
  text-transform: uppercase;
  font-weight: 600;
}

.mobile-container .menu ul li a:hover {
  background: #363636;
}

.mobile-container .menu ul li a:active {
  background: #555;
}

.wrapper {
  height: 100vh;
}

.progressbar {
  position: fixed;
  height: 10px;
  width: 100%;
  background: #56CCF2;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2F80ED, #56CCF2);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2F80ED, #56CCF2);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.progressbar .progress-line {
  height: 10px;
  width: 0;
  background: #0575E6;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #021B79, #0575E6);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #021B79, #0575E6);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.change-color-theme li {
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  list-style: none;
  padding: 5px 0;
}

.change-color-theme li a {
  height: 20px;
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  color: #fff;
  line-height: 1.3;
  border: 1px solid #333;
  width: 150px;
  padding: 8px 0;
  text-transform: uppercase;
}

.change-color-theme li a span.material-icons {
  margin-left: 20px;
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 16px;
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: "liga";
}

.white body {
  background: #fafafa;
}

.white body a {
  color: #09131b;
}

.white body h2 {
  color: #09131b;
}

.white body p {
  color: #09131b;
}

.white body .menu a {
  color: #fafafa;
}

.white body .logo {
  color: #000000;
}

.white body .mobile-container {
  background: #d0c6c6;
}
