.change-color-theme{
  	li{
	  display: flex;
	  align-items: center;
	  align-self: center;
	  justify-content: center;
	  list-style: none;
	  padding: 5px 0;
	  a{

		height: 20px;
		display: flex;
		align-items: center;
		align-self: center;
		justify-content: center;
		color: #fff;
		line-height: 1.3;
		border: 1px solid #333;
		width: 150px;
		padding: 8px 0;
		text-transform: uppercase;
		span.material-icons {
		  margin-left: 20px;
		  font-family: "Material Icons";
		  font-weight: normal;
		  font-style: normal;
		  font-size: 16px;
		  display: inline-block;
		  line-height: 1;
		  text-transform: none;
		  letter-spacing: normal;
		  word-wrap: normal;
		  white-space: nowrap;
		  direction: ltr;

		  /* Support for all WebKit browsers. */
		  -webkit-font-smoothing: antialiased;
		  /* Support for Safari and Chrome. */
		  text-rendering: optimizeLegibility;

		  /* Support for Firefox. */
		  -moz-osx-font-smoothing: grayscale;

		  /* Support for IE. */
		  font-feature-settings: "liga";
		  }
		}
	  }
  }